$offset: .06;
$step: 0.03;
$dots-count: 30;

$half: $dots-count / 2;
@for $i from 1 through $dots-count {
	.dot:nth-child(#{$i}) {
		$transition: '';

		@if ($i > $half) {
			$transition: #{($i + $half) * $step + $offset}s;
		} @else {
			$transition: #{$i * $step * 2 + $offset}s;
		}	

		transition: $transition transform ease-in-out;
		transform: translate3d(0, 0, 0);
	}
}
